import { useState } from "react";
import {
  Button,
  Container,
  Divider,
  Grid,
  Header,
  Icon,
  Image,
  List,
  Menu,
  Segment,
  Sidebar,
  Visibility,
} from "semantic-ui-react";
import PropTypes from "prop-types";
import { HomepageHeading } from "./HomepageHeading";
import { createMedia } from "@artsy/fresnel";
import { MenuItems } from "./MenuItems";

export const DesktopContainer = ({ children }) => {
  const [showFixedMenu, setShowFixedMenu] = useState(false);
  const { Media } = createMedia({
    breakpoints: {
      mobile: 0,
      tablet: 768,
      computer: 1024,
    },
  });

  return (
    <Media greaterThan="mobile">
      <Visibility
        once={false}
        onBottomPassed={() => setShowFixedMenu(true)}
        onBottomPassedReverse={() => setShowFixedMenu(false)}
      >
        <Segment
          inverted
          textAlign="center"
          style={{ minHeight: 700, padding: "1em 0em" }}
          vertical
        >
          <Menu
            fixed={showFixedMenu ? "top" : null}
            inverted={!showFixedMenu}
            pointing={!showFixedMenu}
            secondary={!showFixedMenu}
            size="large"
          >
            <Container>
              <MenuItems />
              <Menu.Item position="right">
                <Button as="a" inverted={!showFixedMenu}>
                  Log in
                </Button>
                <Button
                  as="a"
                  inverted={!showFixedMenu}
                  primary={showFixedMenu}
                  style={{ marginLeft: "0.5em" }}
                >
                  Sign Up
                </Button>
              </Menu.Item>
            </Container>
          </Menu>
          <HomepageHeading />
        </Segment>
      </Visibility>

      {children}
    </Media>
  );
};

DesktopContainer.propTypes = {
  children: PropTypes.node,
};
